/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.set(0, 12.5, 1.5)
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

window.addEventListener('keydown', (e) => {
    switch (e.keyCode) {
        case 37: // Left
            if (!game.done) main.body.applyForce(new CANNON.Vec3(-main.speed, 0, 0), main.body.position)
            break;
        case 39: // Right
            if (!game.done) main.body.applyForce(new CANNON.Vec3(main.speed, 0, 0), main.body.position)
            break;
        case 38: // Up
            if (!game.done) main.body.applyForce(new CANNON.Vec3(0, 0, -main.speed), main.body.position)
            break;
        case 40: // Bottom
            if (!game.done) main.body.applyForce(new CANNON.Vec3(0, 0, main.speed), main.body.position)
            break;
        case 65: // A
            game.enableView = true
            break;
        default:
            break;
    }
})

window.addEventListener('keyup', (e) => {
    game.enableView = false
    switch (e.keyCode) {
        case 32: // Space
            if (!game.done && game.canJump && main.body.position.y < 5.3) main.body.applyForce(new CANNON.Vec3(0, main.speed * 80, 0), main.body.position)
            break;
        case 82: // R
            if (!game.done) reset()
            break;
        case 13: // Enter
            if (game.done) reset()
            break;
        case 67: // C
            console.log(camera.position)
            console.log(camera.rotation)
            break;
        default:
            break;
    }
})

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))


/**
 * Utils
 */
const objectsToUpdate = []

const sphereGeometry = new THREE.SphereBufferGeometry(1, 32, 32)
const boxGeometry = new THREE.BoxBufferGeometry(1, 1, 1, 32, 32)

const createSphere = (radius, position) => {
    const mesh = new THREE.Mesh(
        sphereGeometry,
        new THREE.MeshStandardMaterial({
            metalness: Math.random(),
            roughness: 0.5,
            envMap: environmentMapTexture
        })
    )
    mesh.scale.set(radius, radius, radius)
    mesh.castShadow = true
    mesh.position.copy(position)
    scene.add(mesh)

    const shape = new CANNON.Sphere(radius)
    const body = new CANNON.Body({
        mass: 1,
        position: new CANNON.Vec3(0, 3, 0),
        shape,
        material: defaultMaterial
    })
    //body.addEventListener('collide', playHitSound)
    body.position.copy(position)
    world.addBody(body)

    objectsToUpdate.push({
        mesh: mesh,
        body: body
    })
}

const createBox = (width, height, depth, position) => {
    const mesh = new THREE.Mesh(
        boxGeometry,
        new THREE.MeshStandardMaterial({
            metalness: 0.3,
            roughness: 0.3,
            envMap: environmentMapTexture
        })
    )
    mesh.scale.set(width, height, depth)
    mesh.castShadow = true
    mesh.receiveShadow = true
    mesh.position.copy(position)
    scene.add(mesh)

    const shape = new CANNON.Box(new CANNON.Vec3(width / 2, height / 2, depth / 2))
    const body = new CANNON.Body({
        mass: 300,
        shape,
        material: defaultMaterial
    })
    //body.addEventListener('collide', playHitSound)
    body.position.copy(position)
    world.addBody(body)

    objectsToUpdate.push({
        mesh: mesh,
        body: body
    })
}

