/**
 * Animate
 */
const clock = new THREE.Clock()

let oldElapsedTime = 0

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - oldElapsedTime
    oldElapsedTime = elapsedTime

    // sphereBody.applyForce(new CANNON.Vec3(-0.5, 0, 0), sphereBody.position)
    world.step(1/60, deltaTime, 3)
    
    for (const object of objectsToUpdate) {
        object.mesh.quaternion.copy(object.body.quaternion)
        object.mesh.position.copy(object.body.position)
    }
    main.mesh.quaternion.copy(main.body.quaternion)
    main.mesh.position.copy(main.body.position)
    
    if (!game.enableView) {
        camera.position.z = main.body.position.z + 2
        camera.position.x = main.body.position.x
        controls.target.set( main.body.position.x, main.body.position.y, main.body.position.z )
    }

    plane.rotation.z += 0.03
    finish.rotation.z += 0.03

    if(!game.canJump) {
        const intersects = raycaster.intersectObjects([main.mesh])
        if (intersects.length) {
            game.canJump = true
            plane.visible = false
            TweenMax.to('#guide-jump', 0.5, {
                x: 0,
                opacity: 1
            })
        }
    }
    if(!game.done) {
        const intersects = raycasterFinish.intersectObjects([main.mesh])
        if (intersects.length) {
            game.done = true
            finish.visible = false
            TweenMax.to('#finish', 0.5, {
                x: '-50%',
                y: '-50%',
                opacity: 1
            })
        }
    }


    // Update controls
    controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()